.landingContainer {
  display: flex;
  align-items: center;
  justify-content: right;
  height: 100vh;
  background-image: url("../Images/Backgroundhp.jpg");
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  width: auto;
}

.landingPag {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 160px;
  margin-bottom: 80px;
  width: 300px;
  height: 230px;
  transition: border-radius cubic-bezier(0.075, 0.82, 0.165, 1) 1s,
    transform cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
  
}
.landingPag:hover {
  border-bottom-right-radius: 50px;
  border-top-left-radius: 50px;
  transform: scale(1.5);
  
}

.landingTitle {
  text-align: center;
  font-size: 50px;
  color: white;
}
.landingBtn {
  outline: none;
  cursor: pointer;
  border: none;
  padding: 0.7rem 2rem;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  position: relative;
  display: inline-block;
  letter-spacing: 0.05rem;
  font-weight: 700;
  font-size: 19px;
  border-radius: 500px;
  overflow: hidden;
  background: #e9ede9;
  color: ghostwhite;
}
.landingBtn span {
  position: relative;
  z-index: 10;
  transition: color 0.4s;
}

.landingBtn:hover span {
  color: black;
}

.landingBtn::before,
.landingBtn::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
} 

.landingBtn::before {
  content: "";
  background: #000;
  width: 120%;
  left: -10%;
  transform: skew(30deg);
  transition: transform 0.4s cubic-bezier(0.3, 1, 0.8, 1);
}

.landingBtn:hover::before {
  transform: translate3d(100%, 0, 0);
} 


